.profileCard {
    background-color: #FDFDFD;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    color: inherit;
    display: flex;
    flex-direction: column;
    width: 40vh;
    height: 60vh;
    transition: height 0.3s ease-in-out, width 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    margin: auto;

}

.profileImage{
    padding: 2vh;
    aspect-ratio: 1;
    border-radius: 1%;

}

.credentials p{
    font-size: small;
    width: 100%;
}

.profileCard:hover {
    box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.1);
    width: 43vh;
    height: 63vh;
}


@media only screen and (max-width: 600px) {

    .profileCard {
        width: 25vh;
        max-height: 40vh;
        height: auto;
        margin: auto;
        word-wrap: break-word;
        overflow-wrap: break-word;
        
        
    
    }

    .credentials p{
        font-size: small;
        margin: 2px 0;
        line-height: 1.2;
    }


}


@media only screen and (min-width: 600px) and (max-width: 920px) {
    .profileCard {
        width: 30vh;
        max-height: 45vh;
        height: auto;
        margin: auto;
        word-wrap: break-word;
        overflow-wrap: break-word;
        
        
    
    }

    .credentials p{
        font-size: medium;
    }



}

